import { Component, OnInit } from '@angular/core';
import { OperatorService } from 'src/app/services/operator/operator.service';
import { Router } from '@angular/router';
import { Operator } from 'src/app/models/operator.model';
import Swal from 'sweetalert2';

@Component({
  selector: 'app-add-operator',
  templateUrl: './add-operator.component.html',
  styleUrls: ['./add-operator.component.scss']
})
export class AddOperatorComponent implements OnInit {

  constructor(private operatorService: OperatorService, private router: Router) { }

  ngOnInit(): void {
  }

  createOperator(operatorcode: string, operatorname: string, cashbackPercentageForZpay: string, cashbackPercentageForCustomer: string, operatorstatus: string) {
    let validate = this.validating(operatorcode, operatorname, cashbackPercentageForZpay, cashbackPercentageForCustomer, operatorstatus)
    if (!validate.status) {
      Swal.fire(validate.message, '', 'warning')
      return false
    }
    this.operatorService.createOperator(operatorcode, operatorname, cashbackPercentageForZpay, cashbackPercentageForCustomer, operatorstatus,).subscribe((operator: Operator) => {
      console.log(operator);
      // Now we navigate to /users/user._id
      this.router.navigate(['/operator-list']);
    });

  }

  validating(operatorcode, operatorname, cashbackPercentageForZpay, cashbackPercentageForCustomer, operatorstatus) {
    let res = {
      status: true,
      message: "Validated"
    }
    if (!operatorcode || operatorcode.trim() == "") {
      res.status = false
      res.message = "Operator code required"
      return res
    }
    if (!operatorname || operatorname.trim() == "") {
      res.status = false
      res.message = "Operator name required"
      return res
    }
    if (!cashbackPercentageForZpay || cashbackPercentageForZpay.trim() == "") {
      res.status = false
      res.message = "Zpay cashback required"
      return res
    }
    if (!cashbackPercentageForCustomer || cashbackPercentageForCustomer.trim() == "") {
      res.status = false
      res.message = "Customer cashback required"
      return res
    }
    if (!operatorstatus || operatorstatus.trim() == "") {
      res.status = false
      res.message = "Opertor status required"
      return res
    }
    return res
  }
}
