import { Component, OnInit } from '@angular/core';
import { UserrechargeService } from 'src/app/services/userrecharge/userrecharge.service';
import { ActivatedRoute, Params, Router } from '@angular/router';
import { Recharge } from 'src/app/models/userrecharge.model';

@Component({
  selector: 'app-user-recharge',
  templateUrl: './user-recharge.component.html',
  styleUrls: ['./user-recharge.component.scss']
})
export class UserRechargeComponent implements OnInit {

  recharges: Recharge[];

  selectedUserId: string;

  // constructor(private taskService: TaskService, private router: Router) { }
  constructor(private Rechargeservice: UserrechargeService, private route: ActivatedRoute, private router: Router) { }

  ngOnInit(): void {
    this.route.params.subscribe(

      (params: Params) => {

        console.log(params.userId);
        if (params.userId) {
          this.selectedUserId = params.userId;
          this.Rechargeservice.getRecharges(params.userId).subscribe((recharges: any) => {
            this.recharges = recharges.data;
          })
        }
      }
    )

    // this.WalletbalanceService.getWalletbalances().subscribe((walletEntries: UserWallet[]) => {
    //   this.walletEntries = walletEntries;
    // })
  }

}
