import { Component, OnInit } from '@angular/core';
import { OperatorService } from 'src/app/services/operator/operator.service';
import { ActivatedRoute, Params, Router } from '@angular/router';
import { Operator } from 'src/app/models/operator.model';


@Component({
  selector: 'app-edit-operator',
  templateUrl: './edit-operator.component.html',
  styleUrls: ['./edit-operator.component.scss']
})
export class EditOperatorComponent implements OnInit {

  operators: Operator[];
  operatorId: string;

  constructor(private operatorService: OperatorService, private route: ActivatedRoute, private router: Router) { }

  ngOnInit(): void {
    this.route.params.subscribe(
      (params: Params) => {
        if (params.operatorId) {
          this.operatorId = params.operatorId;
          this.operatorService.getOperator(params.operatorId).subscribe((operators: Operator[]) => {
            this.operators = operators;
          })
        } else {
          this.operators = undefined;
        }
      }
    )
  }
  
  updateOperator(operatorid: string, operatorcode: string, operatorname: string, cashbackPercentageForZpay: string, cashbackPercentageForCustomer: string, operatorstatus: string) {
    this.operatorService.updateUser(operatorid, operatorcode, operatorname, cashbackPercentageForZpay, cashbackPercentageForCustomer, operatorstatus).subscribe((operator: Operator) => {
      console.log(operator);
      // Now we navigate to /users/user._id
      this.router.navigate([ '/operator-list' ]); 
    });
  }

}
