import { Component, OnInit } from '@angular/core';
import { UsersService } from 'src/app/services/user/users.service';
import { ActivatedRoute, Params, Router } from '@angular/router';
import { User } from 'src/app/models/user.model';
import { CommonService } from '../../services/common/common.service'

@Component({
  selector: 'app-dashboard',
  templateUrl: './dashboard.component.html',
  styleUrls: ['./dashboard.component.scss']
})
export class DashboardComponent implements OnInit {

  signups
  recharges
  // signups=0;

  constructor(private usersService: UsersService, private common: CommonService, private route: ActivatedRoute, private router: Router) { }

  ngOnInit(): void {

    let authToken = localStorage.getItem('authToken')
    console.log(authToken)

    // this.common.loadScript()
    this.usersService.getSignup().subscribe((signups) => {
      this.signups = signups;
    })

    this.usersService.getRecharges().subscribe((recharges) => {
      this.recharges = recharges;
    })
  }



}
