import { Component, OnInit } from '@angular/core';
import { UsersService } from 'src/app/services/user/users.service';
import { ActivatedRoute, Params, Router } from '@angular/router';
import { User } from 'src/app/models/user.model';


@Component({
  selector: 'app-edit-user',
  templateUrl: './edit-user.component.html',
  styleUrls: ['./edit-user.component.scss']
})
export class EditUserComponent implements OnInit {
  
  users: User[];
  selectedUserId: string;

  constructor(private usersService: UsersService, private route: ActivatedRoute, private router: Router) { }

  ngOnInit(): void {
    this.route.params.subscribe(
      (params: Params) => {
        if (params.userId) {
          this.selectedUserId = params.userId;
          this.usersService.getUser(params.userId).subscribe((users: User[]) => {
            this.users = users;
          })
        } else {
          this.users = undefined;
        }
      }
    )
  }
  
  updateUser(id: string, username: string, email: string, password: string, phone: string,) {
    this.usersService.updateUser(id, username, email, password, phone).subscribe((user: User) => {
      console.log(user);
      // Now we navigate to /users/user._id
      this.router.navigate([ '/user-list' ]); 
    });
  }

}
