import { AfterViewInit, Component, OnInit } from '@angular/core';
import { WalletService } from 'src/app/services/wallet/wallet.service';
import { UsersService } from 'src/app/services/user/users.service';
import { Router } from '@angular/router';
import { Wallet } from 'src/app/models/wallet.model';
import { User } from 'src/app/models/user.model';
import Swal from 'sweetalert2';
declare var $: any
// import { FormsModule } from '@angular/forms';

@Component({
  selector: 'app-wallet-add',
  templateUrl: './wallet-add.component.html',
  styleUrls: ['./wallet-add.component.scss']
})
export class WalletAddComponent implements OnInit, AfterViewInit {

  users: [];
  walletBalance: number;

  constructor(private usersService: UsersService, private walletService: WalletService, private router: Router) {

  }

  ngOnInit(): void {
    $("#type-select").selectpicker()
    this.InitMethod()
  }
  ngAfterViewInit() {
    $('.user-list').selectpicker();
  }
  async InitMethod() {
    let u = await this.userData()
    this.users = JSON.parse(JSON.stringify(u))

  }
  userData() {
    return new Promise((resolve, reject) => {
      this.usersService.getUsers().subscribe((users: any) => {
        resolve(users.data)
      })
    })
  }
  createWallet(userselectid, type, amount) {
    if (!userselectid || userselectid.trim() == "") {
      Swal.fire('Please select a user to proceed')
      return false
    }
    if (!type || type.trim() == "") {
      Swal.fire('Please select a transaction type')
      return false
    }
    if (amount || amount.trim() !== "") {
      if (parseFloat(amount) <= 0) {
        Swal.fire('Amount should be greater than 0')
        return false
      }
    }

    this.walletService.createNewWallet(userselectid, type, amount).subscribe((response) => {
      console.log(response);
      const wallet = JSON.parse(JSON.stringify(response))
      if (wallet.success == true) {
        Swal.fire(wallet.msg, '', 'success')
      }

      // Now we navigate to /users/user._id
      this.router.navigate(['/user-wallet/' + userselectid]);
    });

  }
  createWallet_backup(userselectid: string, debit: string, credit: string, naration: string) {
    if (!userselectid || userselectid.trim() == "") {
      Swal.fire('Please select a user to proceed')
      return false
    }
    if (debit || debit.trim() !== "") {
      if (parseFloat(debit) <= 0) {
        Swal.fire('Debit should be greater than 0')
        return false
      }
    }
    if (credit || credit.trim() !== "") {
      if (parseFloat(credit) <= 0) {
        Swal.fire('Credit should be greater than 0')
        return false
      }
    }
    if ((!debit || debit.trim() == "") && (!credit || credit.trim() == "")) {
      Swal.fire('Please enter credit or debit to proceed')
      return false
    }
    if (!naration || naration.trim() == "") {
      Swal.fire('Please narate the transaction')
      return false
    }


    this.walletService.createWallet(userselectid, debit, credit, naration).subscribe((response) => {
      console.log(response);
      const wallet = JSON.parse(JSON.stringify(response))
      if (wallet.success == true) {
        Swal.fire(wallet.msg, '', 'success')
      }

      // Now we navigate to /users/user._id
      this.router.navigate(['/user-wallet/' + userselectid]);
    });

  }
  saverange(value: String) {
    console.log(value)
    let users = this.users
    let user = users.find(e => e['_id'] === value);
    // for (var i = 0; i < users.length; i++) {
    // look for the entry with a matching `code` value
    // if (users[i]._id == value) {
    //   // we found it
    //   console.log(users[i])
    //   console.log(users[i].walletBalance)
    //   this.walletBalance = users[i].walletBalance
    // }
    // }
    this.walletBalance = user['walletBalance']
    console.log(user)

  }
}
