import { Injectable } from '@angular/core';
import { WebRequestService } from '../web-request/web-request.service';

@Injectable({
  providedIn: 'root'
})
export class WalletbalanceService {

  constructor(private webReqService: WebRequestService) { }

  getWalletbalances() {
    return this.webReqService.get('wallet/allwalletToday');
  }

  getWalletbalance(walletbalanceId: string) {
    return this.webReqService.get(`walletbalance/${walletbalanceId}`);
  }


  createUser(username: string, email: string, password: string, phone: string) {
    // We want to send a web request to create a list
    return this.webReqService.post('walletbalance', {
      username: username,
      email: email,
      password: password,
      phone: phone,
    });
  }

  updateUser(walletbalanceId: string, username: string, email: string, password: string, phone: string) {
    // We want to send a web request to update a list
    return this.webReqService.patch(`walletbalance/${walletbalanceId}`, {
      username: username,
      email: email,
      password: password,
      phone: phone,
    });
  }

  deletewalletbalance(walletbalanceId: string) {
    console.log(walletbalanceId);
    return this.webReqService.delete(`walletbalance/${walletbalanceId}`);
  }
}
