import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { DashboardComponent } from './pages/dashboard/dashboard.component';
import { AddUserComponent } from './pages/users/add-user/add-user.component';
import { EditUserComponent } from './pages/users/edit-user/edit-user.component';
import { ListUserComponent } from './pages/users/list-user/list-user.component';
import { ListMemberComponent } from './pages/member/list-member/list-member.component';
import { AddMemberComponent } from './pages/member/add-member/add-member.component';
import { EditMemberComponent } from './pages/member/edit-member/edit-member.component';
import { WalletTransactionComponent } from './pages/wallet/wallet-transaction/wallet-transaction.component';
import { WalletBalanceComponent } from './pages/wallet/wallet-balance/wallet-balance.component';
import { ListRechargeComponent } from './pages/recharge/list-recharge/list-recharge.component';
import { ApiRechargeComponent } from './pages/recharge/api-recharge/api-recharge.component';
import { PercentageRechargeComponent } from './pages/recharge/percentage-recharge/percentage-recharge.component';
import { PendingRechargeComponent } from './pages/recharge/pending-recharge/pending-recharge.component';
import { AddOperatorComponent } from './pages/operator/add-operator/add-operator.component';
import { EditOperatorComponent } from './pages/operator/edit-operator/edit-operator.component';
import { ListOperatorComponent } from './pages/operator/list-operator/list-operator.component';
import { LoginComponent } from './pages/login/login.component';
import { UserWalletComponent } from './pages/users/user-wallet/user-wallet.component';
import { UserRechargeComponent } from './pages/users/user-recharge/user-recharge.component';
import { WalletAddComponent } from './pages/wallet/wallet-add/wallet-add.component';
import { LatestRechargeComponent } from './pages/recharge/latest-recharge/latest-recharge.component';
import { AuthGuard } from './guard/auth.guard';

const routes: Routes = [
  { path: '', redirectTo: '/Login', pathMatch: 'full' },
  { path: 'dashboard', component: DashboardComponent, canActivate: [AuthGuard] },
  { path: 'user-add', component: AddUserComponent, canActivate: [AuthGuard] },
  { path: 'user-edit/:userId', component: EditUserComponent, canActivate: [AuthGuard] },
  { path: 'user-wallet/:userId', component: UserWalletComponent, canActivate: [AuthGuard] },
  { path: 'user-recharge/:userId', component: UserRechargeComponent, canActivate: [AuthGuard] },
  { path: 'user-list', component: ListUserComponent, canActivate: [AuthGuard] },
  { path: 'member-list', component: ListMemberComponent, canActivate: [AuthGuard] },
  { path: 'member-add', component: AddMemberComponent, canActivate: [AuthGuard] },
  { path: 'member-edit', component: EditMemberComponent, canActivate: [AuthGuard] },
  { path: 'wallet-transaction', component: WalletTransactionComponent, canActivate: [AuthGuard] },
  { path: 'wallet-balance', component: WalletBalanceComponent, canActivate: [AuthGuard] },
  { path: 'wallet-add', component: WalletAddComponent, canActivate: [AuthGuard] },
  { path: 'recharge-history', component: ListRechargeComponent, canActivate: [AuthGuard] },
  { path: 'latest-recharge', component: LatestRechargeComponent, canActivate: [AuthGuard] },
  { path: 'recharge-api', component: ApiRechargeComponent, canActivate: [AuthGuard] },
  { path: 'recharge-percentage', component: PercentageRechargeComponent, canActivate: [AuthGuard] },
  { path: 'recharge-pending', component: PendingRechargeComponent, canActivate: [AuthGuard] },
  { path: 'operator-add', component: AddOperatorComponent, canActivate: [AuthGuard] },
  { path: 'operator-edit/:operatorId', component: EditOperatorComponent, canActivate: [AuthGuard] },
  { path: 'operator-list', component: ListOperatorComponent, canActivate: [AuthGuard] },
  { path: 'Login', component: LoginComponent },
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
