import { Component, OnInit } from '@angular/core';
import { LoginService } from 'src/app/services/login/login.service';
import { ActivatedRoute, Params, Router } from '@angular/router';
import { HttpErrorResponse, HttpResponse } from '@angular/common/http';
import Swal from 'sweetalert2';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.scss']
})
export class LoginComponent implements OnInit {

  constructor(private loginService: LoginService, private router: Router) { }

  ngOnInit(): void {
  }


  onLoginButtonClicked(email: string, password: string) {
    if (!email || email.trim() == "") {
      Swal.fire('Please enter email ID', '', 'warning')
      return false
    }
    if (!password || password.trim() == "") {
      Swal.fire('Please enter password', '', 'warning')
      return false
    }

    this.loginService.login(email, password).subscribe((res: HttpResponse<any>) => {
      if (res.status === 200) {
        localStorage.setItem('authToken', res.body.data.authToken)
        this.router.navigate(['/dashboard']);
      }
    }, (error: HttpErrorResponse) => {
      Swal.fire(error.error.error, '', 'error')
    });
  }
}
