import { Injectable } from '@angular/core';
import { WebRequestService } from '../web-request/web-request.service';
import { Wallet } from '../../models/wallet.model';

@Injectable({
  providedIn: 'root'
})
export class WallettransactionService {

  constructor(private webReqService: WebRequestService) { }

  getWalletTransactions() {
    return this.webReqService.get('wallet/allwallet');
  }

  getWalletTransaction(trasactionId: string) {
    return this.webReqService.get(`wallettransaction/${trasactionId}`);
  }


  createUser(username: string, email: string, password: string, phone: string) {
    // We want to send a web request to create a list
    return this.webReqService.post('wallettransaction', {
      username: username,
      email: email,
      password: password,
      phone: phone,
    });
  }

  updateUser(trasactionId: string, username: string, email: string, password: string, phone: string) {
    // We want to send a web request to update a list
    return this.webReqService.patch(`wallettransaction/${trasactionId}`, {
      username: username,
      email: email,
      password: password,
      phone: phone,
    });
  }

  deleteUser(trasactionId: string) {
    console.log(trasactionId);
    return this.webReqService.delete(`wallettransaction/${trasactionId}`);
  }
}
