
import { Component, OnInit } from '@angular/core';
import { WalletbalanceService } from 'src/app/services/walletbalance/walletbalance.service';
import { ActivatedRoute, Params, Router } from '@angular/router';
import { WalletBalance } from 'src/app/models/walletbalance.model';
import { environment } from '../../../../environments/environment'
const DATATABLE_URL = environment.dataTableUri
declare var $: any

@Component({
  selector: 'app-wallet-balance',
  templateUrl: './wallet-balance.component.html',
  styleUrls: ['./wallet-balance.component.scss']
})
export class WalletBalanceComponent implements OnInit {

  walletEntries: WalletBalance[];

  selectedwalletbalanceId: string;

  // constructor(private taskService: TaskService, private router: Router) { }
  constructor(private WalletbalanceService: WalletbalanceService, private route: ActivatedRoute, private router: Router) { }

  ngOnInit(): void {
    // this.route.params.subscribe(
    //   (params: Params) => {
    //     if (params.walletbalanceId) {
    //       this.selectedwalletbalanceId = params.walletbalanceId;
    //       this.WalletbalanceService.getWalletbalance(params.walletbalanceId).subscribe((walletEntries: WalletBalance[]) => {
    //         this.walletEntries = walletEntries;
    //       })
    //     } else {
    //       this.walletEntries = undefined;
    //     }
    //   }
    // )

    // this.WalletbalanceService.getWalletbalances().subscribe((walletEntries: any) => {
    //   this.walletEntries = walletEntries.data;
    // })
    this.loadDataTable()
  }


  loadDataTable() {
    $('#wallet-balance-list').DataTable({
      order: [0, "desc"],
      processing: true,
      serverSide: true,
      sScrollX: "100%",
      sScrollXInner: "100%",
      bScrollCollapse: true,
      serverMethod: "post",
      layout: 'fixed',
      bDestroy: true,
      ajax: {
        url: `${DATATABLE_URL}/walletBalance`
      },
      columns: [
        { data: 'sl_no' },
        { data: 'phone' },
        { data: 'service' },
        { data: 'operator' },
        { data: 'naration' },
        {
          data: 'credit',
          render: (a, b, c, d) => {
            return (`<strong><span style="color:#ffffff">+${a}</span></strong>`)
          }
        },
        {
          data: 'debit', render: (a, b, c, d) => {
            return (`<strong><span style="color:#ffffff">-${a}</span></strong>`)
          }
        },
        { data: 'restAmount' }
      ],
      columnDefs: [
        {
          'targets': 5,
          'createdCell': function (td, cellData, rowData, row, col) {
            $(td).css('background', 'green');
          }
        },
        {
          'targets': 6,
          'createdCell': function (td, cellData, rowData, row, col) {
            $(td).css('background', 'brown');
          }
        }
      ]

    });
  }

  onDeleteWalletClick(walletId: string) {
    console.log("naruto");
    this.WalletbalanceService.deletewalletbalance(walletId).subscribe((res: any) => {
      // Now we navigate to /users/user._id
      window.location.reload()
    })
  }

}
