import { Component, OnInit } from '@angular/core';
import { RechargeService } from 'src/app/services/recharge/recharge.service';
import { ActivatedRoute, Params, Router } from '@angular/router';
import { Recharge } from 'src/app/models/recharger.model';
import { CommonService } from 'src/app/services/common/common.service';
import { environment } from '../../../../environments/environment'
const DATATABLE_URL = environment.dataTableUri
declare var $: any

@Component({
  selector: 'app-pending-recharge',
  templateUrl: './pending-recharge.component.html',
  styleUrls: ['./pending-recharge.component.scss']
})
export class PendingRechargeComponent implements OnInit {

  recharges: Recharge[];

  selectedRechargeId: string;

  // constructor(private taskService: TaskService, private router: Router) { }
  constructor(private rechargeService: RechargeService, private common: CommonService, private route: ActivatedRoute, private router: Router) { }

  ngOnInit(): void {
    // this.common.loadScript()
    // this.rechargeService.getPendingRecharges().subscribe((recharges: any) => {
    //   this.recharges = recharges.data;
    // })
    this.loadDataTable()
  }


  loadDataTable() {
    $('#pending-recharge-list').DataTable({
      order: [0, "desc"],
      processing: true,
      serverSide: true,
      sScrollX: "100%",
      sScrollXInner: "100%",
      bScrollCollapse: true,
      serverMethod: "post",
      layout: 'fixed',
      bDestroy: true,
      ajax: {
        url: `${DATATABLE_URL}/pendingrecharge`
      },
      "lengthMenu": [10, 20, 50, 100, 1000],
      columns: [
        { data: 'sl_no' },
        { data: 'name' },
        { data: 'phone' },
        { data: 'mobile' },
        { data: 'amount' },
        { data: 'operator' },
        { data: 'txnId' },
        { data: 'transactionId' },
        { data: 'status' },
        { data: 'apiName' },
        { data: 'operatorRef' },
        { data: 'errormessage' },
        { data: 'createdAt' }
      ],


    });
  }



}
