import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';

@Injectable({
  providedIn: 'root'
})
export class WebRequestService {

  readonly ROOT_URL;

  constructor(private http: HttpClient) {
    // this.ROOT_URL = 'http://localhost:3002/';
    this.ROOT_URL = `https://zpays.in:3002/`
  }

  get(uri: string) {
    const authToken = localStorage.getItem('authToken')
    const headers = new HttpHeaders({
      Authorization: authToken,
      deviceType: "web",
      deviceToken: "1234567890web"
    })

    return this.http.get(`${this.ROOT_URL}${uri}`, { headers });
  }

  post(uri: string, payload: Object) {
    const authToken = localStorage.getItem('authToken')
    const headers = new HttpHeaders({
      Authorization: authToken,
      deviceType: "web",
      deviceToken: "1234567890web"
    })
    return this.http.post(`${this.ROOT_URL}${uri}`, payload, { headers });
  }

  patch(uri: string, payload: Object) {
    return this.http.patch(`${this.ROOT_URL}/${uri}`, payload);
  }

  delete(uri: string) {
    return this.http.delete(`${this.ROOT_URL}/${uri}`);
  }

  login(email: string, password: string) {
    // alert("naruto1" + email)
    // alert(`${this.ROOT_URL}/users/login`)
    // return
    let body = {
      username: email,
      password: password,
      deviceType: "web",
      deviceToken: "1234567890web",
      location: {
        lat: "0.0000000000",
        long: "0.0000000000"
      },
      ipAddress: "1.1.1.1",
      macAddress: "1.1.1.1"
    }

    return this.http.post(`${this.ROOT_URL}user/login/admin`,
      body
      , {
        observe: 'response'
      });
  }

  signup(email: string, password: string) {
    return this.http.post(`${this.ROOT_URL}/users`, {
      email,
      password
    }, {
      observe: 'response'
    });
  }

}
